:root {
    --background-color:#1d1d1d;
    --dark-background: #181818;
    font-size: 16px;
}

html,
body,
#root {
    height: 100%;
    width: 100%;
}

body {
    margin: 0;
    background-color: var(--background-color);
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}

#root {
    display: flex;
}

.bold {
    font-weight: bold;
}

.my-photo {
    height: 100%;
}

#sidebar {
    background-color: var(--dark-background);
}

#main-page {
    flex: 1;
    display: flex;
    align-items: center;
}

.home-name {
    font-size: 3rem;
    padding: 2rem 3rem;
}
